@import url(https://fonts.googleapis.com/css2?family=Lato:wgh@300;400;700&display=swap);
/* styling scrollbars */
::-webkit-scrollbar{
  width: 5px;
  height: 6px;
}
::-webkit-scrollbar-track{
  box-shadow: insert 0 0 5px #a5aaad;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb{
  background: #3ea175;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover{
  background: #a5aaad;

}
/* general styling*/
*{
  margin: 0;
  padding: 0;
}
body{
  box-sizing: border-box;
  font-family: "Lato",sans-serif;
}
.text-primary-p{
  color: #a5aaad;
  font-size: 14px;
  font-weight: 700;
}
.font-bold{
  font-weight: 700;
}
.text-title{
  color: #2e4a66;
}
.text-lightblue{
  color: #469cac;
}
.text-red{
  color: #cc3d38;
}
.text-yellow{
  color: #a98921;
}
.text-green{
  color: #3b9668;
}
.container{
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas: "sidebar nav nav nav" "sidebar main main main";
}
@media only screen and (max-width: 978px){
  .container{
    grid-template-columns: 1fr;
    grid-template-rows:  0.2fr 3fr;
    grid-template-areas: 
    "nav"
    "main";
  }
}
.navbar{
    background: #ffffff;
    grid-area: nav;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 0 30px 0 30px;
    border-bottom: 1px solid lightgray;
}
.nav_icon{
    display: none;
}
.nav_icon > i{
    font-size: 26px;
    color: #a5aaad;
}
.navbar_left > a{
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight: 700;

}
.navbar_left .active_link{
    color: #265acc;
    border-bottom: 3px solid #265acc;
    padding-bottom: 12px;
}
.navbar_right{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.navbar_right > a{
    margin-left: 20px;
    text-decoration: none;
}
.navbar_right >a >i{
    color: #a5aaad;
    font-size: 16px;
    border-radius: 50px;
    background: #ffffff;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #ffffff;
    padding: 7px;
}
@media only screen and (max-width: 978px){
    .nav_icon{
        display: inline;
    }
}
@media only screen and (max-width: 480px) {
    .navbar_left{
        display: none;
    }
}
#sidebar{
    background: #020509;
    grid-area: sidebar;
    overflow-y: auto;
    padding: 20px;
    transition: all 0.5s;
}
.sidebar_title{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: #f3f4f6;
    margin-bottom: 30px;
}
.sidebar_img{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.sidebar_title > div> img{
    width: 75px;
    object-fit: contain;
}
.sidebar_title > div> h1{
    font-size: 18px;
    display: inline;
}
.sidebar_title > i{
    font-size: 18px;
    display: none;
}
.sidebar_menu >h2{
    color: #3ea175;
    font-size: 16px;
    margin-top: 15px;
    margin-bottom: 5px;
    padding: 0 10px;
    font-weight: 700;
}
.sidbar_link{
    color: #f3f4f6;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}
.active_menu_link{
    background: rgba(62, 161, 112, 0.3);
    color: #3ea175;
}
.active_menu_link a{
    color: #3ea175 !important;

}
.sidebar_link >a{
    text-decoration: none;
    color: #a5aaad;
    font-weight: 700;
}
.sidebar_link >i{
    margin-right: 10px;
    font-size: 18px;

}
.sidebar_logout{
    margin-top: 20px;
    padding: 10px;
    color: #e65061;
}
.sidebar_logout >a{
    text-decoration: none;
    color: #e65061;
    font-weight: 700;
    text-transform: uppercase;
}
.sidebar_logout >i{
    margin-right: 10px;
    font-size: 18px;
}
.sidebar_responsive{
    display: inline !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}
@media only screen and (max-width: 978px){
    #sidebar{
        display: none;
    }
    .sidebar_title>i{
        display: inline;
    }
}
main{
    background: #f3f3f6;
    grid-area: main;
    overflow-y: auto;
}
.main_container{
    padding: 20px 35px;
}
.main_title{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.main_title >img{
    max-height: 100px;
    object-fit: contain;
    margin-right: 20px;
}
.main_greeting >h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}
.main_greeting >p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;

}
.main_cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
}
.card{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 70px;
    padding: 25px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;

}
.card_inner{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;

}
.card_inner >span{
    font-size: 25px;
}
.charts{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    margin-top: 50px;
}
.charts_left{
    padding: 25px;
    border-radius: 5px;
    background: #fff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #fff;
}
.charts_left_title{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.charts_left_title > div> h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}
.charts_left_title > div>p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}
.charts_left_title > i{
    color: #ffff;
    font-size: 20px;
    background: #ffc100;
    border-radius: 200px 0px 200px 200px;
    -moz-border-radius: 200px 0px 200px 200px;
    -webkit-border-radius: 200px 0px 200px 200px;
    border: 0px solid #000000;
    padding: 15px;
}
.charts_right{
    padding: 25px;
    border-radius: 5px;
    background: #ffff;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #ffff;
}
.charts_right_title{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.charts_right_title >div >h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}
.charts_right_title >div> p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}
.charts_right_title > i{
    color: #fff;
    font-size: 20px;
    background: #39447a;
    border-radius: 200px 0px 200px 200px;
    border: 0px solid #000000;
    padding: 15px;
}
.charts_right_cards{
    display: grid;
